body, html {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.wrapper {
  max-width: 1200px;
  width: 100%;
  box-sizing: border-box;
  background: #c0dbd9;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
}

.main {
  height: 100%;
}

.btn {
  color: #fff;
  cursor: pointer;
  background: #a5c7f9;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  font-size: 20px;
  font-weight: 400;
}

.header {
  background: #7faff7;
  padding: 30px;
}

.dropdown {
  position: relative;
}

.popup-checkbox, .popup {
  display: none;
}

.popup-checkbox:checked + .popup {
  opacity: 1;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.popup {
  min-width: 400px;
  min-height: 400px;
  z-index: 9;
  background-color: #fff;
  transition: all .3s;
  position: absolute;
  bottom: -15px;
  left: 0;
  transform: translateY(100%);
}

@media (max-width: 420px) {
  .popup {
    min-width: 200px;
    min-height: 200px;
  }
}

.popup span {
  font-size: 53px;
}

.popup--close {
  color: #fff;
  cursor: pointer;
  background: #7faff6;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  font-size: 20px;
  text-decoration: none;
  position: absolute;
  top: 15px;
  right: 15px;
}

.section {
  margin-top: 15px;
}

.section-1 {
  grid-gap: 15px;
  counter-reset: list;
  background: #b3f95a;
  grid-template-rows: auto;
  grid-template-columns: repeat(6, 1fr);
  grid-template-areas: "a a b b b c"
                       "d d d d d e";
  display: grid;
}

.section-1 .box-content {
  min-height: 200px;
  color: #fff;
  background: #8ad191;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 53px;
  font-weight: 400;
  display: flex;
}

.section-1 .box-content:before {
  counter-increment: list;
  content: counter(list);
}

.section-1 .box-content:nth-child(1) {
  grid-area: a;
}

.section-1 .box-content:nth-child(2) {
  grid-area: b;
}

.section-1 .box-content:nth-child(3) {
  grid-area: c;
}

.section-1 .box-content:nth-child(4) {
  grid-area: d;
}

.section-1 .box-content:nth-child(5) {
  grid-area: e;
}

.section-2 {
  box-sizing: border-box;
  grid-gap: 2.7%;
  background: #88b5bf;
  grid-template-rows: auto;
  grid-template-columns: repeat(4, 1fr);
  padding: 2.5%;
  display: grid;
}

.section-2 .box-content {
  width: 100%;
  background: #0d89b0;
  padding-top: 77.79%;
  position: relative;
}

.footer {
  background: #7faff7;
  margin-top: 100px;
  padding: 30px;
}

